}

.content .content-overlay {
  background: rgba(0,0,0,0.7);
  position: absolute;
  height: 98.7%;
  width: 97%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-overlay{
  opacity: 1;
}

.content-image{
